import axios from "axios";
import { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";

const BookingAdminDashboard = () => {
  const { id } = useParams();

  const [showModal, setShowModal] = useState(false);
  const [bookingStatement, setBookingStatement] = useState(null);

  const [showStatusModal, setShowStatusModal] = useState(false);
  const [selectedBooking, setSelectedBooking] = useState(null); // stores the clicked item object
  const [newStatus, setNewStatus] = useState(""); // stores dropdown value

  const [centreBookings, setCentreBookings] = useState([]);

  // Pagination states
  const [currentPage, setCurrentPage] = useState(1);
  const [itemsPerPage, setItemsPerPage] = useState(10);

  useEffect(() => {
    // Get userId from localStorage

    if (!id) {
      console.error("No centreId found in URL");
      return;
    }

    // API call to backend
    axios
      .get(`${process.env.REACT_APP_BACKEND_URL}/api/bookings/centre/${id}`)
      .then((response) => {
        setCentreBookings(response.data.bookings); // store fetched data
        setCurrentPage(1); // Reset to first page when data changes
      })

      .catch((error) => {
        console.error("Error fetching bookings:", error);
      });
  }, [id]);

  const updatingStatus = async (id, updatedStatus) => {
    axios
      .patch(`${process.env.REACT_APP_BACKEND_URL}/api/bookings/${id}/status`, {
        status: updatedStatus,
      })
      .then((res) => {

        // update local state
        setCentreBookings((prev) =>
          prev.map((booking) => ({
            ...booking,
            services: booking.services.map((service) =>
              service._id === id
                ? { ...service, status: updatedStatus }
                : service
            ),
          }))
        );

        setShowStatusModal(false);
      })
      .catch((err) => {
        console.error("Error updating status:", err);
        alert("Failed to update status");
      });
  };

  const [status, setStatus] = useState("");

  // ðŸ” Fetch bookings whenever status changes
  useEffect(() => {
    const fetchBookings = async () => {
      try {
        const response = await axios.get(
          `${process.env.REACT_APP_BACKEND_URL}/api/bookings/searchBooking/Admin`,
          {
            params: { status, centreId: id }, // send both filters
          }
        );

        setCentreBookings(response.data.bookings || []);
        setCurrentPage(1); // Reset to first page when data changes
      } catch (err) {
        console.error("Error fetching bookings:", err);
      }
    };

    fetchBookings();
  }, [status, id]);

  // Pagination logic
  const flattenedBookings = centreBookings.flatMap((booking) =>
    booking.services.map((service) => ({
      ...service,
      bookingData: booking,
    }))
  );

  const totalItems = flattenedBookings.length;
  const totalPages = Math.ceil(totalItems / itemsPerPage);

  const startIndex = (currentPage - 1) * itemsPerPage;
  const endIndex = startIndex + itemsPerPage;
  const currentBookings = flattenedBookings.slice(startIndex, endIndex);

  // Group back into original structure for display
  const displayBookings = currentBookings.reduce((acc, item) => {
    const existingBooking = acc.find((b) => b._id === item.bookingData._id);
    if (existingBooking) {
      existingBooking.services.push(item);
    } else {
      acc.push({
        ...item.bookingData,
        services: [item],
      });
    }
    return acc;
  }, []);

  const handlePageChange = (page) => {
    setCurrentPage(page);
  };

  const handleItemsPerPageChange = (newItemsPerPage) => {
    setItemsPerPage(parseInt(newItemsPerPage));
    setCurrentPage(1); // Reset to first page when changing items per page
  };

    const handlePageChangeWithScroll = (page) => {
  handlePageChange(page); // your existing page change logic
  window.scrollTo({ top: 0, behavior: "smooth" }); // scroll to top smoothly
};

  const renderPagination = () => {
    if (totalPages <= 1) return null;

    const pages = [];
    const maxVisible = 4;
    let startPage = Math.max(1, currentPage - Math.floor(maxVisible / 2));
    let endPage = Math.min(totalPages, startPage + maxVisible - 1);

    if (endPage - startPage + 1 < maxVisible) {
      startPage = Math.max(1, endPage - maxVisible + 1);
    }

      //first button
         pages.push(
    <li
      key="first"
      className={`page-item ${currentPage === 1 ? " disabled" : ""}`}
    >
      <button
        className="page-link"
        onClick={() => currentPage !== 1 && handlePageChangeWithScroll(1)}
       
      >
        First
      </button>
    </li>
  );

    // Previous button
    pages.push(
      <li
        key="prev"
        className={`page-item ${currentPage === 1 ? "disabled" : ""}`}
      >
        <button
          className="page-link"
          onClick={() => currentPage > 1 && handlePageChangeWithScroll(currentPage - 1)}
          disabled={currentPage === 1}
        >
         
          Prev
        </button>
      </li>
    );

    // Page numbers
    for (let i = startPage; i <= endPage; i++) {
      pages.push(
        <li
          key={i}
          className={`page-item ${currentPage === i ? "active" : ""}`}
        >
          <button className="page-link" onClick={() => handlePageChangeWithScroll(i)}>
            {i}
          </button>
        </li>
      );
    }

    // Next button
    pages.push(
      <li
        key="next"
        className={`page-item ${currentPage === totalPages ? "disabled" : ""}`}
      >
        <button
          className="page-link flx-align gap-2 flex-nowrap"
          onClick={() =>
            currentPage < totalPages && handlePageChangeWithScroll(currentPage + 1)
          }
          disabled={currentPage === totalPages}
        >
          Next
      
        </button>
      </li>
    );
         pages.push(
    <li
      key="last"
      className={`page-item ${currentPage === totalPages ? "disabled" : ""}`}
    >
      <button
        className="page-link"
        onClick={() => handlePageChangeWithScroll(totalPages)}
      >
        Last
      </button>
    </li>
  );

    return pages;
  };

  useEffect(() => {
  if (showModal) {
    // Disable background scroll
    document.body.style.overflow = "hidden";
  } else {
    // Enable it again when modal closes
    document.body.style.overflow = "auto";
  }

  // Cleanup (in case component unmounts)
  return () => {
    document.body.style.overflow = "auto";
  };
}, [showModal]);

  return (
    <div className="dashboard-body__content">
      {/* ========================= Statement section start =========================== */}
      <div className="row gy-4">
        <div className="col-12">
          <div className="card common-card border border-gray-five">
            <div
              className="col-sm-4 col-xs-6"
              style={{
                display: "flex",
                gap: "5px",
                marginLeft: "auto",
                width: "fit-content",
                marginTop: "20px",
                marginRight: "30px",
              }}
            >
              {/* <input
  type="text"
  className="common-input border-gray-five"
  // value={name}
  // onChange={(e) => setName(e.target.value)}
  placeholder="Search by Centre Name"
/> */}

              <select
                id="time"
                className="common-input border-gray-five"
                defaultValue="all"
                value={status}
                onChange={(e) => setStatus(e.target.value)}
              >
                <option value="">Select Status</option>
                <option value="Cancelled">Cancelled</option>
                <option value="Completed">Completed</option>
                <option value="pending">Upcoming</option>
              </select>

              {/* <button style={{ backgroundColor: "green", borderRadius: "3px", padding: "5px",height:"50px",width:"100px" }} onClick={handleSearch}>Search</button> */}
            </div>

            <div className="card-body">
              <div className="table-responsive">
                <table className="table text-body mt--24">
                  <thead>
                    <tr>
                      <th>Booking Date</th>
                      <th>Booking ID</th>
                      <th>Appointment Date</th>
                      <th>Appointment Time</th>
                      <th>Patient Name</th>
                      <th>Patient Email</th>
                      <th>Patient Mobile</th>
                      <th>Centre Name</th>
                      <th>Service Type</th>
                      <th>Service Name</th>
                      <th>Price</th>
                      <th>Platform Fees</th>
                      <th>Status</th>
                      <th className="text-center">Action</th>
                    </tr>
                  </thead>
                  <tbody>
                    {displayBookings.length === 0 ? (
                      <tr>
                        <td colSpan="14" className="text-center text-muted">
                          No bookings found
                        </td>
                      </tr>
                    ) : (
                      displayBookings.map((booking, idx) =>
                        booking.services.length === 0 ? (
                          <tr key={`empty-${idx}`}>
                            <td colSpan="14" className="text-center text-muted">
                              No services available for this booking
                            </td>
                          </tr>
                        ) : (
                          booking.services.map((item, index) => (
                            <tr key={`${idx}-${index}`}>
                              <td>
                                {new Date(booking.bookingDate).toLocaleString()}
                              </td>
                              <td>{item.bookingId}</td>
                              <td>{item.appointmentDate}</td>
                              <td>{item.appointmentTime}</td>
                              <td>
                                {booking.userDetails.firstName}{" "}
                                {booking.userDetails.lastName}
                              </td>
                              <td>{booking.userDetails.email}</td>
                              <td>{booking.userDetails.mobileNumber}</td>
                              <td>{item.centre.name}</td>
                              <td>{item.type}</td>
                              <td>{item.name}</td>
                              <td>
                                â‚¹
                                {item?.discountPrice
                                  ? item?.discountPrice
                                  : item?.actualPrice}
                              </td>
                              <td>â‚¹{item.platformFees}</td>
                              <td>
                                <span
                                  className={`badge ${
                                    item.status?.toLowerCase() === "confirmed"
                                      ? "bg-success"
                                      : item.status?.toLowerCase() ===
                                        "cancelled"
                                      ? "bg-danger"
                                      : item.status?.toLowerCase() ===
                                        "completed"
                                      ? "bg-success"
                                      : item.status?.toLowerCase() === "pending"
                                      ? "bg-warning"
                                      : "bg-secondary"
                                  }`}
                                >
                                {item.status=="pending" ?"Upcoming":item.status}
                                </span>
                              </td>
                              <td style={{ display: "flex", gap: "10px" }}>
                                <button
                                  type="button"
                                  className="btn btn-sm btn-primary"
                                  onClick={() => {
                                    setShowModal(true);
                                    setBookingStatement({ booking, item });
                                  }}
                                >
                                  View
                                </button>
                              </td>
                            </tr>
                          ))
                        )
                      )
                    )}
                  </tbody>
                </table>

                {/* Pagination Controls */}
               
              </div>
               <div className="flx-between gap-2 mt-4">
                  <div className="paginate-content flx-align flex-nowrap gap-3">
                    <select
                      className="select common-input py-2 px-3 w-auto"
                      value={itemsPerPage}
                      onChange={(e) => handleItemsPerPageChange(e.target.value)}
                    >
                      <option value={5}>5 per page</option>
                      <option value={10}>10 per page</option>
                      <option value={15}>15 per page</option>
                      <option value={20}>20 per page</option>
                      <option value={25}>25 per page</option>
                    </select>
                    <span className="paginate-content__text fs-14">
                      Showing {startIndex + 1} -{" "}
                      {Math.min(endIndex, totalItems)} of {totalItems}
                    </span>
                  </div>
                  <nav aria-label="Page navigation example">
                    <ul className="pagination common-pagination mt-0">
                      {renderPagination()}
                    </ul>
                  </nav>
                </div>
            </div>
          </div>
        </div>
      </div>

      {showModal && bookingStatement && (
        <div
          style={{
            position: "fixed",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: "rgba(0,0,0,0.5)",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            zIndex: 1000,
             padding:"15px",
              overflowY:"auto",
          }}
          onClick={() => setShowModal(false)}
        >
          <div
            style={{
              backgroundColor: "#fff",
              borderRadius: "10px",
              maxWidth: "700px",
              width: "90%",
              boxShadow: "0 0 10px rgba(0,0,0,0.3)",
              position: "relative",
              overflow: "hidden",
              maxHeight:"90vh",
              overflowY:"auto",
            }}
            onClick={(e) => e.stopPropagation()}
          >
            {/* Header */}
            <div
              style={{
                backgroundColor: "#3a3a3a",
                color: "#fff",
                padding: "15px 20px",
                fontSize: "18px",
                fontWeight: "bold",
                position:"sticky",
                top: 0,
                zIndex: 10,
              }}
            >
              Booking Details
              <button
                onClick={() => setShowModal(false)}
                style={{
                  position: "absolute",
                  top: "10px",
                  right: "10px",
                  background: "transparent",
                  border: "none",
                  fontSize: "22px",
                  color: "#fff",
                  cursor: "pointer",
                }}
              >
                &times;
              </button>
            </div>

            {/* Grid layout */}
            <div
              style={{
                display: "grid",
                gridTemplateColumns: "1fr 1fr",
                gap: "20px",
                padding: "20px",
              }}
            >
              {/* Left Column */}
              <div>
                <div style={{ marginBottom: "20px" }}>
                  <p>
                    <strong style={{ color: "black" }}>Booking ID</strong>
                  </p>
                  <p>{bookingStatement.item.bookingId}</p>
                </div>

                <div style={{ marginBottom: "20px" }}>
                  <p>
                    <strong style={{ color: "black" }}>Appointment Date</strong>
                  </p>
                  <p>{bookingStatement.item.appointmentDate}</p>
                </div>
                <div style={{ marginBottom: "20px" }}>
                  <p>
                    <strong style={{ color: "black" }}>Appointment Time</strong>
                  </p>
                  <p>{bookingStatement.item.appointmentTime}</p>
                </div>
                <div style={{ marginBottom: "20px" }}>
                  <p>
                    <strong style={{ color: "black" }}>Patient Name</strong>
                  </p>
                  <p>
                    {bookingStatement.booking.userDetails.firstName}{" "}
                      {bookingStatement.booking.userDetails.lastName}
                  </p>
                </div>
                <div style={{ marginBottom: "20px" }}>
                  <p>
                    <strong style={{ color: "black" }}>Patient Email</strong>
                  </p>
                  <p>{bookingStatement.booking.userDetails.email}</p>
                </div>
                <div style={{ marginBottom: "20px" }}>
                  <p>
                    <strong style={{ color: "black" }}>Patient Mobile</strong>
                  </p>
                  <p>{bookingStatement.booking.userDetails.mobileNumber}</p>
                </div>
              </div>

              {/* Right Column */}
              <div>
                <div style={{ marginBottom: "20px" }}>
                  <p>
                    <strong style={{ color: "black" }}>Booking Type</strong>
                  </p>
                  <p>{bookingStatement.item.type}</p>
                </div>

                <div style={{ marginBottom: "20px" }}>
                  <p>
                    <strong style={{ color: "black" }}>
                      {bookingStatement.item.type === "package"
                        ? "Package Name"
                        : "Service Name"}
                    </strong>
                  </p>
                  <p>
                    {bookingStatement.item.name}
                  </p>
                </div>

                <div style={{ marginBottom: "20px" }}>
                  {bookingStatement.item.type === "package" ? (
                    <p>
                      <strong style={{ color: "black" }}>Services Name</strong>
                    </p>
                  ) : (
                    <p>
                      <strong style={{ color: "black" }}>Category Name</strong>
                    </p>
                  )}

                  {bookingStatement?.item?.type === "package" ? (
                    bookingStatement?.item?.includedServices?.map(
                      (service, key) => <p key={key}>{service}</p>
                    )
                  ) : (
                    <p>{bookingStatement?.item?.category}</p>
                  )}
                </div>

                <div style={{ marginBottom: "20px" }}>
                  <p>
                    <strong style={{ color: "black" }}>Doctors Name</strong>
                  </p>
                  {bookingStatement.item.doctors.map((doc, index) => (
                    <p key={index}>{doc}</p>
                  ))}
                </div>

                <div style={{ marginBottom: "20px" }}>
                  <p>
                    <strong style={{ color: "black" }}>Price</strong>
                  </p>
                  <p>â‚¹{bookingStatement.item.totalPrice}</p>
                </div>
                <div style={{ marginBottom: "20px" }}>
                  <p>
                    <strong style={{ color: "black" }}>Status</strong>
                  </p>
                  <p>{bookingStatement.item.status==="pending"?"Upcoming":bookingStatement.item.status}</p>
                </div>
              </div>
            </div>

            {/* Full-width Description */}
            {/* <div style={{ padding: "0 20px 20px 20px" }}>
        <div style={{ marginTop: "10px" }}>
          <p><strong style={{ color: "black" }}>Description</strong></p>
          <p>{bookingStatement.description || "No description provided."}</p>
        </div>
      </div> */}
          </div>
        </div>
      )}

      {showStatusModal && selectedBooking && (
        <div
          style={{
            position: "fixed",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: "rgba(0,0,0,0.5)",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            zIndex: 1000,
          }}
        >
          <div
            style={{
              backgroundColor: "#fff",
              borderRadius: "10px",
              maxWidth: "500px",
              width: "90%",
              boxShadow: "0 0 10px rgba(0,0,0,0.3)",
              position: "relative",
              padding: "20px",
            }}
          >
            {/* Header */}
            <div
              style={{
                backgroundColor: "#3a3a3a",
                color: "#fff",
                padding: "15px 20px",
                fontSize: "18px",
                fontWeight: "bold",
                borderTopLeftRadius: "10px",
                borderTopRightRadius: "10px",
                margin: "-20px -20px 20px -20px",
                display: "flex",
                justifyContent: "space-between",
                alignItems: "center",
              }}
            >
              Update Details
              <button
                onClick={() => setShowStatusModal(false)}
                style={{
                  background: "transparent",
                  border: "none",
                  fontSize: "22px",
                  color: "#fff",
                  cursor: "pointer",
                }}
              >
                &times;
              </button>
            </div>

            {/* Status dropdown */}
            <div style={{ marginBottom: "20px" }}>
              <label style={{ fontWeight: "bold", color: "black" }}>
                Status
              </label>
              <select
                value={newStatus}
                onChange={(e) => setNewStatus(e.target.value)}
                style={{
                  width: "100%",
                  padding: "10px",
                  border: "1px solid #ccc",
                  borderRadius: "5px",
                  marginTop: "5px",
                }}
              >
                <option value="">-- Select Status --</option>
                <option value="Cancelled">Cancelled</option>
                <option value="Completed">Completed</option>
              </select>
            </div>

            {/* Footer with update button */}
            <div
              style={{
                display: "flex",
                justifyContent: "flex-end",
                marginTop: "20px",
              }}
            >
              <button
                onClick={() => {
                  updatingStatus(selectedBooking._id, newStatus);
                }}
                style={{
                  backgroundColor: "#007bff",
                  color: "#fff",
                  padding: "8px 16px",
                  border: "none",
                  borderRadius: "5px",
                  cursor: "pointer",
                }}
              >
                Update
              </button>
            </div>
          </div>
        </div>
      )}

      {/* ========================= Statement section End =========================== */}
    </div>
  );
};

export default BookingAdminDashboard;
